import SelectTime from "../../plugin/components/SelectTime";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import UserHandler from "../../script/handlers/UserHandler";
import InputArea from "../../plugin/components/InputArea";
import InfoTableItem from "../../plugin/components/InfoTableItem";

export default {
  name: "change_coins",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    SelectTime,
    InfoTable,
    PermissionButton
  },
  data() {
    return {
      pagesize: 10,
      currentPage: 1,
      total: 0,
      dialogFormVisible: false,
      formLabelWidth: "120px",
      format: {
        play_id: ""
      },
      tableStyle: [
        { label: "玩家ID", prop: "uid", width: "" },
        { label: "修改类型", prop: "type", width: "" },
        { label: "修改途径", prop: "path", width: "" },
        { label: "修改数量", prop: "value", width: "" },
        { label: "备注", prop: "remarks", width: "" },
        { label: "操作者", prop: "auth", width: "" },
        { label: "操作时间", prop: "updated_at", width: "" },
      ],
      records: [],
      form: {
        play_id: "",
        mod_type: "",
        mod_path: "",
        mod_num: "",
        remark: ""
      },
      pageInfo: new PageInfo(0, [5, 10, 15], 5)
    };
  },
  methods: {
    getModGoldList() {
      let params = {
        page: this.currentPage,
        limit: this.pagesize,
        user_id: Number(this.format.play_id)
      }
      this.$http.get('v1/backend/operation/coin-modify',{
        params
      }).then(res=>{
        console.log(res)
        if(res.data.code === 200) {
          this.records = res.data.data
          this.total = res.data.total
        }
      })
    },
    modPlayGoldFn() {
      let data = {
        user_id: Number(this.form.play_id),
        modify_type: Number(this.form.mod_type),
        modify_path: Number(this.form.mod_path),
        modify_value: Number(this.form.mod_num),
        remarks: this.form.remark
      }
      this.$http.post('v1/backend/operation/coin-modify',data).then(res=>{
        console.log(res)
        if(res.data.code === 200) {
          this.dialogFormVisible = false
          this.getModGoldList()
          this.$message({
            type: "success",
            message: res.data.msg
          })
        }
      })
    },
    /**搜索*/
    search() {},
    handleEdit(index, row) {
      console.log(index, row);
    },
    handleDelete(index, row) {
      console.log(row);
      const ids = row.id.toString();
      console.log(ids);
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(
          this.$message({
            type: "success",
            message: res.data.msg
          })
        )
        .catch(() => {
          this.$message({
            type: "info",
            message: res.data.msg
          });
        });
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.getModGoldList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getModGoldList();
    },
  },
  mounted() {
    this.getModGoldList()
  }
};



